// Rotated & Flipped Icons
// -------------------------

.#{$mi-css-prefix}-rotate-90 {
    @include mi-icon-rotate(90deg, 1);
}

.#{$mi-css-prefix}-rotate-180 {
    @include mi-icon-rotate(180deg, 2);
}

.#{$mi-css-prefix}-rotate-270 {
    @include mi-icon-rotate(270deg, 3);
}

.#{$mi-css-prefix}-flip-horizontal {
    @include mi-icon-flip(-1, 1, 0);
}

.#{$mi-css-prefix}-flip-vertical {
    @include mi-icon-flip(1, -1, 2);
}
// Hook for IE8-9
// -------------------------

:root .#{$mi-css-prefix}-flip-horizontal,
:root .#{$mi-css-prefix}-flip-vertical,
:root .#{$mi-css-prefix}-rotate-180,
:root .#{$mi-css-prefix}-rotate-270,
:root .#{$mi-css-prefix}-rotate-90 {
    filter: none;
}