/* FONT PATH
 * -------------------------- */
@font-mice {
    font-family: 'micon_nb';
    src: url('#{$mi-font-path}micon_nb.eot?v=#{$mi-version}');
    src: url('#{$mi-font-path}micon_nb.eot?#iefix&v=#{$mi-version}') format('embedded-opentype'),
    url('#{$mi-font-path}micon_nb.woff2?v=#{$mi-version}') format('woff2'),
    url('#{$mi-font-path}micon_nb.woff?v=#{$mi-version}') format('woff'),
    url('#{$mi-font-path}micon_nb.ttf?v=#{$mi-version}') format('truetype'),
    url('#{$mi-font-path}micon_nb.svg?v=#{$mi-version}#micon_nb') format('svg');
    font-weight: normal;
    font-style: normal;
}